<template>
	<view>
	
		<view class="cu-bar bg-cyan search">
				<view class="action" >
					<text class="cuIcon-back" @click="Back()"></text>
					返回
				</view>
			<view class="search-form radius">
				<text class="cuIcon-search"></text>
				<input  v-model.trim="keyword" :adjust-position="false" type="text" placeholder="输入关键词" confirm-type="search"></input>
			</view>
			<view class="action" @click="Search()">
				<text class="cuIcon-search"></text>
				<text>搜索</text>
			</view>
		</view>
		<view v-if="knowItem.length>0" class="cu-card article" :class="true?'no-card':''">
			
			<template v-for="(item,index) in knowItem">
			<view class="cu-item shadow">
				<view class="title"><view class="text-cut">{{item.name}}</view></view>
				<view class="content">
					<image :src="item.imageUrls|pictureFilter"
					 mode="aspectFill"></image>
					<view class="desc">
						<view class="text-content"> {{item.shortDescription}}</view>
						<view>
							<view class="cu-tag bg-red light sm round">{{item.creationTimeDateBeforeNow}}</view>
							<view class="cu-tag bg-green light sm round">热点:{{item.vistorCount}}</view>
						</view>
					</view>
				</view>
			</view>
		</template>
		</view>
		<view v-else class="solids-bottom padding-xs flex align-center bg-white">
			<view class="flex-sub text-center">
			
				<view style="min-height:100vh" class="padding">没有相关内容,请重新搜索</view>
			</view>
		</view>
		<view style="height:120upx"></view>
	</view>
</template>


<script>
	export default {
		data() {
			return {
				
				knowItem:[],
				keyword:""
			}
		},
		async onLoad(options){
			this.keyword=options.keyword;
		},
		async onShow(){
			await this.KnowLedgeList();
		},
		methods: {
			async KnowLedgeList() {
				let url = "/WxApi/KnowLedgeWx/KnowLedgeList";
				
		        this.$comm.ShowLoading();
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {name:this.keyword});
				if (success == true) {
					this.knowItem = items;
				}
				uni.hideLoading()
			},		
		
			async tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				await this.KnowLedgeList();
				
				
			},
			async Search()
			{
				await this.KnowLedgeList();
			},
			async Back()
			{
				uni.navigateBack({
					delta:1
				})
			}
		}
	}
</script>

